.App {
  text-align: center;
  font-family: Arial, sans-serif;
  margin: 20px;
}

.chat-box {
  border: 1px solid #ccc;
  padding: 10px;
  height: 300px;
  width: 100%;
  max-width: 500px;
  margin: 0 auto 20px;
  overflow-y: auto;
  background: #f9f9f9;
}

.user p {
  text-align: right;
  color: blue;
}

.bot p {
  text-align: left;
  color: green;
}

.input-area {
  display: flex;
  justify-content: center;
  gap: 10px;
}

input {
  width: 300px;
  padding: 10px;
  font-size: 16px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
}
